@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;700&display=swap);
body.competenz{font-family:"Helvetica Neue", sans-serif}.cv-builder-columns{display:grid;grid-template-columns:1fr 3fr;max-width:1550px;margin:0 auto}.cv-builder-columns h3{font-weight:400;font-size:1.5rem;color:var(--darkblue)}.cv-builder-columns h4{font-weight:400;font-size:1rem}.cv-builder-columns h1,.cv-builder-columns h2,.cv-builder-columns h3,.cv-builder-columns h4{margin:1em 0 0.8em 0}.cv-builder-columns p{margin:0 0 0.5em 0}@media (max-width: 800px){.cv-builder-columns{grid-template-columns:1fr}}.layout-large-left .sidebar-cnt{width:calc(100% + 20vw)}.layout-large-left .cv-builder-cnt .cv-builder-content{display:-webkit-flex;display:flex}.layout-large-left .cv-builder-cnt .cv-builder{margin:0 2rem;-webkit-transform:scale(0.7) translateY(3rem);transform:scale(0.7) translateY(3rem)}@media (min-width: 1500px){.layout-large-left .cv-builder-cnt .cv-builder{-webkit-transform:scale(0.7) translate(-15%, 0rem);transform:scale(0.7) translate(-15%, 0rem)}}.layout-large-left .cv-builder-cnt .cv-builder .cv-ui-button{opacity:0}.cv-builder-cnt{width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.cv-builder-cnt .cv-builder-content{padding:4rem 0}.cv-builder-cnt .cv-builder{max-width:1200px;min-height:1100px;background-color:var(--white);margin:0 30px;border-radius:1rem;box-shadow:2px 5px 15px -3px rgba(0,0,0,0.3);padding:40px;transition:-webkit-transform 0.6s cubic-bezier(0.37, 0.15, 0.48, 0.99);transition:transform 0.6s cubic-bezier(0.37, 0.15, 0.48, 0.99);transition:transform 0.6s cubic-bezier(0.37, 0.15, 0.48, 0.99), -webkit-transform 0.6s cubic-bezier(0.37, 0.15, 0.48, 0.99);-webkit-transform-origin:top right;transform-origin:top right}.cv-builder-cnt .cv-builder .section{margin-top:3rem;cursor:pointer}.cv-builder-cnt .cv-builder input{width:100%}.cv-builder-cnt .cv-builder .huge-input{font-size:clamp(2rem, 5vw, 3rem);background:inherit;outline:none;border:none;color:var(--darkblue);font-weight:700;line-height:1em;cursor: