 .menu-button .cancel-button{opacity:0.8;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.sidebar-cnt .sidebar .menu-button .menu-button-cnt{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;width:100%;text-align:left;padding:0.5rem 1rem}.sidebar-cnt .sidebar .menu-button .menu-button-cnt .icon{margin-right:1rem;-webkit-transform:translateY(5px);transform:translateY(5px)}.sidebar-cnt .sidebar .menu-button .panel-content{padding:0 3.8rem 0}.sidebar-cnt .sidebar .menu-button .panel-content .style-thumb{width:100%;height:0;padding-bottom:120%;background-color:var(--lightgrey);border-radius:3px}.help-bubble-focus{position:relative}.help-bubble{position:absolute;background-color:var(--purple);padding:2rem;border-radius:var(--radius);box-shadow:2px 5px 15px rgba(0,47,59,0.3),inset 5px 5px 15px rgba(255,255,255,0.121569);bottom:-68px;right:calc(100% + 2.5rem);width:100%;max-width:300px}.help-bubble h3,.help-bubble h4,.help-bubble p{color:white}.help-bubble h3{margin-top:0}.help-bubble::after{content:"";position:absolute;bottom:50px;right:-20px;width:0;height:0;border-left:2rem solid var(--purple);border-bottom:2rem solid transparent;border-top:2rem solid transparent}.help-bubble .suggestion{padding:0.15rem 0.5rem;border-radius:1rem;border:1px solid var(--dark-white);color:var(--dark-white);margin-right:0.5em;margin-bottom:0.5em;font-size:12px}.dark-text{color:var(--darkblue)}.light-text{color:var(--lightblue)}.pop-up-bg{z-index:10;background:rgba(66,66,66,0.418);-webkit-backdrop-filter:blur(2px);backdrop-filter:blur(2px);width:100%;height:100vh;position:fixed;top:0;left:0}.pop-up-cnt{width:100%;max-width:100%;height:100vh;position:fixed;top:0;left:0;overflow-y:auto}.pop-up-cnt .pop-up{position:relative;padding:2rem;margin:4rem 2rem;background:var(--white);border-radius:var(--radius);z-index:11;box-shadow:0 5px 15px var(--shadow);width:100%;max-width:700px}@media (max-width: 900px){.pop-up-cnt .pop-up{width:70%}.pop-up-cnt .pop-up .two-column-grid{grid-template-columns:1fr}}.buttons-cnt{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end}.items-form .editable-list-items{list-style:none;padding:0}.items-form .editable-list-items .list-item{position:relative;padding:1rem;background:var(--off-white);margin:1rem 0;display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;border-radius:var(--radius)}.items-form .editable-list-items .list-item .prop{margin-right:2rem}.items-form .editable-list-items .list-item .cancel-button{opacity:0.6;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.add-new-item-cnt{padding:1.5rem;border-radius:var(--radius);border:1px solid var(--lightblue)}.add-new-item-cnt .subtitle{color:var(--lightblue);padding-bottom:1rem}ul{padding-left:1rem}.cancel-button{position:absolute;top:0;right:0;cursor:pointer}.cancel-button:hover .subtitle{color:#dc1414}.help-button{cursor:pointer;margin-left:-1rem}.help-button:hover .subtitle{color:var(--purple)}.two-column-grid{display:grid;grid-template-columns:1fr 1fr;grid-gap:2rem}.subtitle{font-size:0.8rem;color:grey}.flex{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}.flex.align-center{-webkit-align-items:center;align-items:center}.flex.justify-center{-webkit-justify-content:center;justify-content:center}.flex.space-between{-webkit-justify-content:space-between;justify-content:space-between}.grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(120px, 1fr));grid-gap:1rem}body{--white: #fff;--off-white: #f0f0f0;--dark-white: #e6e6e6;--lightgrey: rgb(208, 208, 208);--lightblue: #6ebcd9;--darkblue: #074b64;--purple: #574170;--tint-primary: #def6ff;--text: rgb(56, 56, 56);--shadow: rgba(0, 0, 0, 0.4);--item-shadow: 2px 2px 3px rgba(0, 0, 0, 0.3);--hover-shadow: 2px 3px 15px rgba(0, 0, 0, 0.3);--radius: 7px;--font: Poppins, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;font-size:14px;background-color:var(--off-white);color:var(--text);margin:0;font-family:var(--font);font-weight:400;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}input{font-family:var(--font)}.overflow-cnt{overflow:hidden}

